.success {
    border-color: green;
    background-color: green;
    color:white;
    border-width: 5px 5px 5px 5px;
    font-size: 2rem;
    text-align: center;
    width: 100%;
    margin: 0px auto;
    padding-top: 5px;
    padding-bottom: 5px;
}

.error {
    border-color: red;
    background-color: red;
    color:white;
    border-width: 5px 5px 5px 5px;
    font-size: 2rem;
    text-align: center;
    width: 100%;
    margin: 0px auto;
    padding-top: 5px;
    padding-bottom: 5px;
}